/**
 * 消息弹窗工厂
 *
 * @file
 * @author Yangholmes
 */
import MessageBox from './MessageBox';
import Vue from 'vue';

export default options => {
    let props = Object.assign({}, options);
    console.log(props);
    let MB = Vue.extend({
        render(h) {
            return h(MessageBox, {
                props
            });
        },
        methods: {
            onShow() {
                this.$children[0].onShow();
            }
        }
    });
    let mbFactory = () => {
        let messageBox = new MB().$mount();
        document.body.appendChild(messageBox.$el);
        return messageBox;
    };

    return mbFactory();
};
